import { LitElement, html } from "lit"
import { customElement } from "lit/decorators.js"
import { DraftComponentMixin } from "../common/mixins/DraftComponentMixin.js"

import componentStyle from "../common/styles/Component.css"
import style from "./NotificationGroup.css"

/**
 * Notification group is used to position and style a group of notifications.
 *
 * @status draft
 * @category feedback
 * @slot - Default slot in which to place notifications.
 */
@customElement("nord-notification-group")
export default class NotificationGroup extends DraftComponentMixin(LitElement) {
  static styles = [componentStyle, style]

  render() {
    return html`
      <div class="n-notification-group">
        <slot></slot>
      </div>
    `
  }
}

declare global {
  interface HTMLElementTagNameMap {
    "nord-notification-group": NotificationGroup
  }
}
